body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto','Ubuntu',sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(./unsplash.jpg);
}
.container{
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.top{
  text-align: center;
}

.result{
  text-align: center; 
}
.menu{
  // margin-top: 200px;
  display: flex;
  flex-direction: column;
  // align-items: center;
  // flex: 1;
}
.quiz{
  width:60%;
  // padding: 10 50px;
}

.heading{
  font-size: 4rem;
  margin-bottom: .1em;
}

.dropdown{
  height: 2rem;
  border-radius: 5px;
  background-color: white;
  font-family: inherit;
  font-size: inherit;
  margin-bottom: 15px;
  margin-left: 10px;
}

ul{
  padding-inline-start: 0px;
  margin-block-start: 0px;
}
li{
  list-style-type: none;
  border-radius: 5px;
  padding: 10px 20px 10px 10px;
  background-color: transparentize($color: #636F88, $amount: 0.6);
  margin-bottom:10px;
}

.hoverEffectEnable:hover{
  cursor: pointer;
  background-color:#636F88;
}

.button{
  margin-top: 10px;
  background-color: rgba(0,0,0,0.75);
  width: fit-content;
  color: white;
  padding: .5rem 1rem;
  border: 1px solid rgba(0,0,0,0.75);
  border-radius: 10px;
  font-size: 1.4rem;
  cursor: pointer;
  transition: background-color 250ms;
  float: right;
    &:hover{
      background-color: rgba(0,0,0,0.85);
    }
}
.correct{
  background-color: transparentize($color: #308051, $amount: 0.6);
}
.wrong{
  background-color: transparentize($color: #7e1b1b, $amount: 0.6);
}
i{
  position: absolute;
}
a{
  text-decoration: none;
  color: white;
}
.category{
  min-width: 20rem;
}
label{
  font-weight: 500;
  font-size: 20px;
}
.footer{
  padding: 10px;
}
